
@import url('https://fonts.googleapis.com/css2?family=Oxanium:wght@200&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');

*{
  box-sizing: border-box;
}
#root{
  position: absolute;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}
h2{
  position: relative;
  display: inline-block;
  font-family: 'Bebas Neue';
  font-size: calc(32px + 3vw) !important;
  color: #f5f38e;
  text-shadow: 0 0 0.0625em #ffffff,0 0 0.75em #f5f38e,  0 0 0.25em #fcbc7d, 0 0 0.125em #fc7dc7;
  
}
h1{
  position: relative;
  display: inline-block;
  font-family: 'Bebas Neue';
  font-size: calc(64px + 4vw) !important;
  color: #ffffff;
  text-shadow: 0 0 0.0625em #ffffff,0 0 0.75em #f5f38e,  0 0 0.25em #fcbc7d, 0 0 0.125em #fc7dc7;
  
  animation: neon-animation 5s infinite alternate;
}
h1::before{
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 250%;
  background: #fcbc7d;
  transform: perspective(2em) rotateX(30deg);
  filter: blur(2.75em);
  animation: neon-shadow-animation 5s infinite alternate;
  z-index: -1;
}
@keyframes neon-animation {
  0%{
    color: white;
  }
  15%{
    color: #f5f38e;
  }
  100%{
    color: #f5f38e;
  }
}
@keyframes neon-shadow-animation {
  0%{
    background: #f5f38e;
    height: 225%;
  }
  15%{
    background: #fcbc7d;
    height: 250%;
  }
  100%{
    background: #fcbc7d;
    height: 250%;
  }
}

button{
  display: block;
  margin-left: auto !important;
  margin-right: auto !important;
  
  font-family: 'Bebas Neue' !important;
  /*background: linear-gradient(45deg,#f5f38e, #f5f38e 15%,  #fcbc7d , #fc7d7d, #fc7dc7, #c97dfc, #fc7dc7, #fc7d7d, #fcbc7d 85%) ;
  background-size: 600%;
  background-position: left;
  border: 1px solid black;
  */
  border: 0.25rem solid hsl(59 84% 76% / 0.25);
  background: transparent;
  font-size:2rem !important;
  padding: 0.5rem 1.5rem;
  color: hsl(59 84% 76% / 0.25);
  /*text-shadow: 0 0 0.25em #ffffff,0 0 0.5em #f5f38e,  0 0 0.5em #fcbc7d, 0 0 0.25em #fc7dc7;
  */
  border-radius: 10px !important;
  transition: 500ms ease-in-out;
  

  /*transition: color ease 500ms;*/
}
button:hover, button:focus{
  box-shadow: inset 0 0 0.5em #f5f38e, 0 0 0.5em #ffffff;
  border-color: white;
  color: white;
  border: 0.25rem solid white;
  text-shadow: 0 0 0.25em #ffffff,0 0 0.5em #f5f38e,  0 0 0.5em #fcbc7d, 0 0 0.25em #fc7dc7;
  
}
