.trapezoid {
  position: absolute;
  left: 25vw;
  top:160px;
  border-bottom: 30vw solid rgb(156, 156, 156);
  border-left: 20vw solid transparent;
  border-right: 20vw solid transparent;
  height: 0;
  z-index: -1;
}
.t-black{
  border-bottom-color: black;
  z-index: -2;
  border-bottom-width: calc(30vw + 5px);
  left: calc(25vw - 5px);
}
.t-shadow{
  z-index: -5;
  top:100px;
  border-bottom: 20vw solid rgba(0, 0, 0, 0.068);
  border-left: 23vw solid transparent;
  border-right: 23.5vw solid transparent;
  border-bottom-width: calc(32vw + 5px);
  left: calc(22vw - 5px);
}

img{
  position:absolute;
  top:0;
  left:calc(50vw - 140px);
}

body{
  overflow: hidden;
  /*background: linear-gradient(45deg, #f5f38e,  #fcbc7d , #fc7d7d,#fc7dc7, #c97dfc);*/
  background-color: black !important;
  background-position:right;
  background-size: 800% !important;
  width: auto;
  min-height: 100vh;
  animation: bg-animation 100s infinite alternate;
}
@keyframes bg-animation {
  0%{
    background-position: left;
  }
  100%{
    background-position: right;
  }
}

audio{
  position:absolute;
  z-index: 100;
}

