.score-counter{
  width: 15vw;
  height: 15vw;
  border-top: solid 0.5vw rgb(126, 76, 19);
  border-bottom: solid 0.5vw rgb(126, 76, 19);
  
  border-right: solid 0.2vw rgb(126, 76, 19);
  
  border-left: solid 0.2vw rgb(126, 76, 19);
  background-color: beige;
  /*background-color: rgb(207, 196, 134);*/
  border-radius: 35% 35% 5% 5%;
  overflow: hidden;
}
.score-counter p{
  margin-top: 0.25vw;
  margin-bottom: 0.25vw;
  font-size: 1vw;
}
.score{
  display: inline-block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 0;
  height: 1.5vw;
  background-color: white;
  border: solid 2px black;
  width: fit-content;
}
